.overlay-editor-wrapper{
	display:none;
}
.overlay-editor-wrapper[data-active="true"]{
	padding-top:35px;
	display:block;
	position:absolute;
	inset:0;
	z-index:9;
	background:white;
}

#overlay-editor-root{
	background:white;
}

/*-------*/

.overlay-editor-layout{
	box-sizing:border-box;
	position:relative;
	display:grid;
	grid-template-rows:auto 1fr auto;
	grid-template-columns:1fr 300px;
}
.overlay-editor-layout * {
	box-sizing:inherit;
}
.overlay-editor-header{
	grid-column:1/-1;
	grid-row: 1/2;
}
.overlay-editor-blocks{
	grid-row:2/3;
	grid-column:1/2;
}
.overlay-editor-footer{
	grid-row:3/4;
	grid-column:1/-1;
}
.overlay-editor-sidebar{
	grid-row:2/3;
	grid-column:2/3;
}

/*---------------*/

.overlay-editor-sidebar .components-panel__body{
	padding-block:20px;
}
.overlay-editor-sidebar .setting-field{
	padding-inline:20px;
}
